import MxDatePicker from "@/components/mx-datepicker/mx-datepicker.vue";
import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	components: {
		MxDatePicker
	},
	data() {
		return {
			conslist: [],
			loadingText: '正在加载...',
			hasmore: true,
			curpage: 1,
			//控制滑动效果
			theIndex: null,
			oldIndex: null,
			isStop: false,
			showPicker: false,
			range: [new Date().toISOString().slice(0, 10), new Date().toISOString().slice(0, 10)],
			type: 'rangedate',
			value: ''
		}
	},
	computed: mapState(['hasLogin', 'userInfo']),
	onLoad() {
		console.log('页面加载');
		this.loadData();
	},
	onReachBottom() {
		console.log('下拉加载...');
		this.curpage++;
		this.loadData();
	},
	onPageScroll() {
		console.log('页面滚动...');
	},
	onPullDownRefresh() {
		console.log('上拉刷新...');
		uni.stopPullDownRefresh();
	},
	methods: {
		async loadData() {
			var _this = this;
			console.log('===userInfo===' + this.hasLogin);
			console.log(this.userInfo);
			if (!this.hasLogin) {
				uni.navigateTo({
					url: 'login',
				})
			} else {
				if (!this.hasmore) {
					this.loadingText = '暂无更多信息信息';
					//return false;
				}
				_this.$Request.post('member_order/orderlist', {
					curpage: _this.curpage,
					daterange:_this.value
				}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.hasmore = res.hasmore;
						console.log(_this.hasmore);
						let _conslist = res.datas.order_list;
						_this.conslist = [];
						for (var i = 0; i < _conslist.length; i++) {
							_this.conslist.push(_conslist[i]);
						}
						if (!this.hasmore) {
							this.loadingText = '暂无更多信息信息';
							//return false;
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: res.datas.error,
							duration: 2000
						});
						uni.navigateTo({
							url: 'login',
						});
					}
				});
			}
		},
		onShowDatePicker(type) { //显示
			this.type = type;
			console.log(type);
			this.showPicker = true;
			this.value = this[type];
		},
		onSelected(e) { //选择
			var _this = this;
			this.showPicker = false;
			if (e) {
				this[this.type] = e.value;
				this.value = e.value;
				//选择的值
				console.log('value => ' + e.value);
				//原始的Date对象
				console.log('date => ' + e.date);
			}
			_this.loadData();
		},
		toPage: function(url) {
			uni.navigateTo({
				url: url,
			})
		},
		toStore: function(id) {
			uni.navigateTo({
				url: '/pages/store/index?store_id=' + id,
			})
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			console.log('====touchStart====');
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			console.log('====touchMove====');
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},
		touchEnd(index, $event) {
			console.log('====touchEnd====');
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		deleteOrder(orderid) {
			var _this = this;
			console.log('====deleteOrder====');
			_this.$Request.post('member_order/orderdelete', {
				orderid: orderid
			}).then(res => {
				if (res.code == 200) {
					console.log(res);
					let len = _this.conslist.length;
					for (let i = 0; i < len; i++) {
						if (orderid == _this.conslist[i].order_id) {
							_this.conslist.splice(i, 1);
							break;
						}
					}
					/* this.selectedList.splice(this.selectedList.indexOf(orderid), 1);
					this.sum(); */
					_this.oldIndex = null;
					_this.theIndex = null;
				} else {
					uni.showToast({
						icon: 'none',
						title: res.datas.error,
						duration: 2000
					});
				}
			});

		}

	}
}
